let that

class Tab {
  constructor(id) {
    that = this
    // Ëé∑ÂèñÂÖÉÁ¥†
    this.main = document.querySelector(id)
    this.add = this.main.querySelector(".tabAdd")
    this.ul = this.main.querySelector("header :first-child")
    this.tabcon = this.main.querySelector("main")
    this.init()
  }

  // ÂàùÂßãÂåñ
  init() {
    this.updateNode()
    this.add.onclick = this.addTab
    for (let i = 0; i < this.lis.length; i++) {
      this.lis[i].index = i
      this.lis[i].onclick = this.toggleTab
      this.remove[i].onclick = this.removeTab
      this.spans[i].ondblclick = this.editTab
      this.sections[i].ondblclick = this.editTab
    }
  }

  // Ëé∑ÂèñÊâÄÊúâÂÖÉÁ¥†
  updateNode() {
    this.lis = this.main.querySelectorAll("li")
    this.sections = this.main.querySelectorAll("section")
    this.remove = this.main.querySelectorAll(".remove")
    this.spans = this.main.querySelectorAll(".nav li span:first-child")
  }

  // ÂàáÊç¢
  toggleTab() {
    that.clearClass()
    this.className = "active"
    that.sections[this.index].className = "conactive"
  }

  // Ê∏ÖÈô§
  clearClass() {
    for (let i = 0; i < this.lis.length; i++) {
      this.lis[i].className = ""
      this.sections[i].className = ""
    }
  }

  // Ê∑ªÂä†
  addTab() {
    that.clearClass()
    let li = "<li class='active'><span>Êñ∞ÈÄâÈ°π</span><span class='remove'>x</span></li>"
    let tab = "<section class='conactive'>ÊµãËØïn</section>"
    that.ul.insertAdjacentHTML("beforeend", li)
    that.tabcon.insertAdjacentHTML("beforeend", tab)
    that.init()
  }

  // Âà†Èô§
  removeTab(e) {
    e.stopPropagation()
    let index = this.parentNode.index
    that.lis[index].remove()
    that.sections[index].remove()
    if (document.querySelector(".active")) return
    index--
    that.lis[index] && that.lis[index].click()
  }

  // ‰øÆÊîπ
  editTab() {
    let str = this.innerHTML
    window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty()
    this.innerHTML = `<input type='input' value='${str}' />`
    const input = this.children[0]
    input.select()
    input.onblur = function () {
      this.parentNode.innerHTML = this.value
    }
    input.onkeyup = function (e) {
      if (e.keyCode === 13) this.blur()
    }
  }
}

let Tab1 = new Tab("#tab")